.container {
  width: 568x;
  flex-direction: column;
  display: flex;
  height: 756px;
  gap: 10px;
  margin-top: 100px;
  margin-left: 10px;
}

.scroll {
  width: 568x;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  height: 540px;
  gap: 10px;
}

.top {
  height: 86px;
  margin: 0 17px 0 36px;
}

.bottom {
  height: 86px;
  margin: 0 17px 0 36px;
}

.scroll::-webkit-scrollbar {
	width: 12px;
}
.scroll::-webkit-scrollbar-track {
	background: #2F2F37;
}
.scroll::-webkit-scrollbar-thumb {
	background: #8585AD;
	border: 3px solid #2F2F37;
	border-radius: 6px;
}

.drag {
  margin: 0 8.5px 0 13.5px;
  cursor: grab;
  transition: all .1s ease-in;
  cursor: grab;
}

.drag:hover {
  opacity: .6;
  filter: drop-shadow(0px 4px 32px rgba(51, 51, 255, 0.5));
}

.box {
      padding: 16px 24px;
    display: inline-block;
    border-radius: var(--common-border-radius-s);
    background: var(--background-element);
    max-width: 536px;
    width: 100%;
    min-height: 80px;
    font-family: 'Jet Brains Mono';
    font-size: 16px;
    line-height: 24px;
    color: #F2F2F3;
    flex-grow: 1;


}

